'use client';

import React, { useState, useEffect } from 'react';
import { VentaServicio } from '../../../entities/venta';

type Props = {
  open: boolean;
  venta: VentaServicio | null;
  onClose: () => void;
  onGuardar: (ventaActualizada: VentaServicio) => void;
};

const VentaEditarModal = ({ open, venta, onClose, onGuardar }: Props) => {
  const [formData, setFormData] = useState<VentaServicio | null>(null);

  useEffect(() => {
    if (venta) {
      // Clonamos la venta para evitar mutaciones directas
      setFormData({ ...venta });
    }
  }, [venta]);

  if (!open || !formData) return null;

  const handleClienteChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prev) =>
      prev ? {
        ...prev,
        cliente: {
          ...prev.cliente,
          [name]: value,
        },
      } : prev
    );
  };

  const handleFechaChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setFormData((prev) => (prev ? { ...prev, fechaRegistro: value } : prev));
  };

  const handleCostoChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = parseFloat(e.target.value);
    setFormData((prev) => (prev ? { ...prev, costoTotal: isNaN(value) ? 0 : value } : prev));
  };

  const handleGuardarClick = () => {
    if (formData) {
      onGuardar(formData);
    }
  };

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50">
      <div className="bg-white p-6 rounded-xl shadow-lg w-[600px] max-h-[90vh] overflow-y-auto">
        <h2 className="text-xl font-bold mb-4">
          Editar Venta #{formData.idVentaServicio}
        </h2>

        <div className="grid grid-cols-1 gap-4">
          <label className="text-sm font-medium">
            Nombre del Cliente:
            <input
              type="text"
              name="nombre"
              value={formData.cliente.nombre}
              onChange={handleClienteChange}
              className="mt-1 w-full border p-2 rounded"
            />
          </label>

          <label className="text-sm font-medium">
            Correo:
            <input
              type="email"
              name="correo"
              value={formData.cliente.correo}
              onChange={handleClienteChange}
              className="mt-1 w-full border p-2 rounded"
            />
          </label>

          <label className="text-sm font-medium">
            Teléfono:
            <input
              type="tel"
              name="telefono"
              value={formData.cliente.telefono}
              onChange={handleClienteChange}
              className="mt-1 w-full border p-2 rounded"
            />
          </label>

          <label className="text-sm font-medium">
            Fecha de Venta:
            <input
              type="date"
              value={formData.fechaRegistro?.slice(0, 10)} // formato YYYY-MM-DD
              onChange={handleFechaChange}
              className="mt-1 w-full border p-2 rounded"
            />
          </label>

          <label className="text-sm font-medium">
            Total (S/):
            <input
              type="number"
              value={formData.costoTotal}
              onChange={handleCostoChange}
              className="mt-1 w-full border p-2 rounded"
              min="0"
              step="0.01"
            />
          </label>

          <div>
            <p className="text-sm font-medium mb-1">Servicios Incluidos:</p>
            <ul className="list-disc ml-5 text-sm text-gray-700 space-y-1">
              {formData.detalles.map((detalle, i) => (
                <li key={i}>
                  {detalle.servicio?.nombre ?? 'Servicio'} x{detalle.cantidad}
                </li>
              ))}
            </ul>
            {/* Nota: Puedes agregar campos editables para detalles si lo necesitas */}
          </div>
        </div>

        <div className="mt-6 flex justify-end gap-3">
          <button
            onClick={handleGuardarClick}
            className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
          >
            Guardar
          </button>
          <button
            onClick={onClose}
            className="px-4 py-2 bg-gray-300 text-gray-700 rounded hover:bg-gray-400"
          >
            Cancelar
          </button>
        </div>
      </div>
    </div>
  );
};

export default VentaEditarModal;
